.container {
  padding: .2em;
}

.container.grid {
  width: 50%;
}
.container.list { 
  width: 100%;
}

.video {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  border: 1px solid lightgray;
  box-shadow: 3px 3px 5px 0px rgba(191,191,191,0.53);
  cursor: pointer;
  transition: transform 250ms ease-in;
}

.video:hover{
  transform: scale(1.02);
}

.thumbnail {
  width: 40%;
  height: 100%;
}

.metadata {
  margin-left: .2em;
}

.title, .channel {
  margin: 0;
  font-size: .8rem;
}

.channel {
  font-size: .6rem;
}